@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Rubik+Iso&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Tilt+Prism&display=swap');


.home {
  max-width: 100% !important;
  width: 100%;
  margin: 0;
  overflow: hidden !important;

  .home-intro {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 74vh;
    background-size: cover;
    background-position: top;
    padding: 0 5rem;
    padding-top: 8rem;

    &.hidden {
      opacity: 0;
    }

    &.show {
      opacity: 1;

      .content {
        animation: fadeInLeft 2s ease forwards;
      }

      .hero-icon {
        animation: fadeInRight 2s ease forwards;
      }
    }

    @media (min-width: 760px) and (max-width: 1024px) {
      flex-direction: column;
      height: 100vh;
      width: 93vw;
      padding: 0 2rem;
      padding-top: 12rem;
    }

    @media (min-width: 480px) and (max-width: 759px) {
      flex-direction: column;
      height: 100vh;
      max-width: 100vw;
      padding: 0 1.2rem;
      padding-top: 12rem;
    }

    @media (max-width: 479px) {
      flex-direction: column;
      height: 100vh;
      max-width: 100vw;
      padding: 0 0.5rem;
      padding-top: 14rem;
      text-align: center;
    }

    .content {
      text-align: start;
      width: 55%;
      padding-right: 6rem;

      @media (min-width: 760px) and (max-width: 1024px) {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding-right: 0;
      }

      @media (min-width: 480px) and (max-width: 759px) {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding: 0;
        margin-bottom: 2rem;
      }

      @media (max-width: 479px) {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 0;
      }

      .content-primary {
        margin-bottom: 2rem;

        h1 {
          margin: 0;
          font-weight: 900;
          text-transform: capitalize;
          letter-spacing: 0.02em;
          font-size: 2.5rem !important;

          @media (min-width: 1025px) and (max-width: 1300px) {
            font-size: 3.2rem;
          }

          @media (min-width: 760px) and (max-width: 1024px) {
            font-size: 2.9rem;
          }

          @media (min-width: 480px) and (max-width: 759px) {
            font-size: 2.6rem;
          }

          @media (max-width: 479px) {
            font-size: 2.3rem;
          }

          span {
            font-family: 'Tilt Prism', cursive;
            font-size: 3rem;
            color: #ff7800 !important;
            text-shadow: 3px 3px 0 #1f2127,
              -1px -1px 0 #1f2127, 1px -1px 0 #1f2127,
              -1px 1px 0 #1f2127, 1px 1px 0 #1f2127;
            padding: 0 8px;

            @media (min-width: 1025px) and (max-width: 1300px) {
              font-size: 3.9rem;
            }

            @media (min-width: 760px) and (max-width: 1024px) {
              font-size: 3.4rem;
            }

            @media (min-width: 480px) and (max-width: 759px) {
              font-size: 3rem;
            }

            @media (max-width: 479px) {
              font-size: 2.6rem;
            }
          }
        }
      }

      .content-secondary {
        p {
          margin: 0;
          font-weight: 700;
          text-transform: lowercase;
          font-size: 18px;
          line-height: 30px;
          letter-spacing: 0.04em;
          opacity: 0.8;

          @media (min-width: 1025px) and (max-width: 1300px) {
            font-size: 16px;
            line-height: 26px;
          }

          @media (min-width: 760px) and (max-width: 1024px) {
            font-size: 15px;
          }

          @media (min-width: 480px) and (max-width: 759px) {
            font-size: 14px;
            line-height: 21px;
          }

          @media (max-width: 479px) {
            font-size: 13px;
            line-height: 20px;
          }
        }
      }
    }

    .hero-icon {
      width: 45%;
      padding-bottom: 1rem;

      @media (min-width: 760px) and (max-width: 1024px) {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
      }

      @media (min-width: 480px) and (max-width: 759px) {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        padding-bottom: 2rem;
      }

      @media (max-width: 479px) {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        padding-bottom: 3rem;
      }

      svg {
        width: 500px;
        height: 366px;

        @media (min-width: 1025px) and (max-width: 1300px) {
          width: 450px;
          height: 329px;
        }

        @media (min-width: 760px) and (max-width: 1024px) {
          width: 430px;
          height: 314px;
        }

        @media (min-width: 480px) and (max-width: 759px) {
          width: 400px;
          height: 292px;
        }

        @media (max-width: 479px) {
          width: 300px;
          height: 219px;
        }
      }
    }
  }

  .showroom {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 90vh;
    width: 100%;

    @media (min-width: 1025px) and (max-width: 1300px) {
      height: 100vh;
    }

    @media (min-width: 760px) and (max-width: 1024px) {
      height: 90vh;
    }

    @media (min-width: 480px) and (max-width: 759px) {
      height: 100vh;
    }

    @media (max-width: 479px) {
      height: 70vh;
    }

    .showroom-background {
      object-fit: contain;
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      height: 100%;
      width: 100%;
      filter: blur(8px);
      -webkit-filter: blur(8px);
    }
  }
}

.showroom-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 5rem;

  @media (min-width: 480px) and (max-width: 759px) {
    margin-top: 3rem;
  }

  @media (max-width: 479px) {
    margin-top: 2rem;
  }

  &.hidden {
    opacity: 0;
  }

  &.show {
    opacity: 1;

    .showroom-title {
      animation: flipInY 2s ease forwards;
      backface-visibility: visible !important;
    }

    .showroom {
      animation: fadeInLeft 2s ease forwards;
    }
  }

  .showroom-title {
    background: #12141d;
    border-radius: 12px;
    padding: 0.8rem 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 5rem;
    box-shadow: 0px 0px 4px 1px rgba(20, 233, 86, 1) inset;
    -webkit-box-shadow: 0px 0px 4px 1px rgba(20, 233, 86, 1) inset;
    -moz-box-shadow: 0px 0px 4px 1px rgba(20, 233, 86, 1) inset;
    transform: rotate(0.01turn);

    @media (min-width: 1025px) and (max-width: 1300px) {
      padding: 0.7rem 1.7rem;
    }

    @media (min-width: 760px) and (max-width: 1024px) {
      padding: 0.6rem 1.5rem;
    }

    @media (min-width: 480px) and (max-width: 759px) {
      padding: 0.5rem 1.3rem;
    }

    @media (max-width: 479px) {
      padding: 0.4rem 1.1rem;
    }

    h2 {
      text-transform: uppercase;
      font-family: 'Cyborg';
      font-weight: 900;
      color: #14e956;
      text-shadow: 3px 3px 0 #000, -1px -1px 0 #000,
        1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;

      @supports (-webkit-text-stroke: 0.1px #000) {
        -webkit-text-stroke: 0.1px #000;
        -webkit-text-fill-color: #14e956;
      }

      @media (min-width: 1025px) and (max-width: 1300px) {
        font-size: 2.5rem;
      }

      @media (min-width: 760px) and (max-width: 1024px) {
        font-size: 2.2rem;
      }

      @media (min-width: 480px) and (max-width: 759px) {
        font-size: 2rem;
      }

      @media (max-width: 479px) {
        font-size: 1.8rem;
      }
    }
  }

  .button-wrap {
    height: 20vh;
    display: flex;
    align-items: center;
    justify-content: center;

    @media (min-width: 1025px) and (max-width: 1300px) {
      height: 16vh;
    }

    @media (min-width: 760px) and (max-width: 1024px) {
      height: 14vh;
    }

    @media (min-width: 480px) and (max-width: 759px) {
      height: 12vh;
    }

    @media (max-width: 479px) {
      height: 11vh;
    }

    .butt {
      padding: 1rem 3rem;
      text-transform: uppercase;
      font-family: 'Cyborg';
      font-weight: 900;
      font-size: 2.2rem;
      background: #14e956;
      background-color: #14e956;
      background-image: linear-gradient(315deg,
          #14e956 0%,
          #72f29a 100%);
      border: none;
      border-radius: 7px;
      box-shadow: 9px 7px 32px -11px rgba(20, 233, 86, 0.74);
      transition: all 0.3s ease-in-out 0s;
      cursor: pointer;
      outline: none;
      position: relative;

      @media (min-width: 1025px) and (max-width: 1300px) {
        padding: 0.7rem 2.3rem;
        font-size: 1.9rem;
      }

      @media (min-width: 760px) and (max-width: 1024px) {
        padding: 0.6rem 1.9rem;
        font-size: 1.5rem;
      }

      @media (min-width: 480px) and (max-width: 759px) {
        padding: 0.5rem 1.5rem;
        font-size: 1.2rem;
      }

      @media (max-width: 479px) {
        padding: 0.4rem 1.3rem;
        font-size: 0.9rem;
      }

      &::before {
        content: '';
        border-radius: 7px;
        width: calc(450px + 12px);
        height: calc(80px + 12px);
        border: 6px solid #72f29a;
        box-shadow: 0 0 60px rgba(20, 233, 86, 0.64);
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        opacity: 0;
        transition: all 0.3s ease-in-out 0s;

        @media (min-width: 1025px) and (max-width: 1300px) {
          width: calc(400px + 12px);
          height: calc(70px + 12px);
        }

        @media (min-width: 760px) and (max-width: 1024px) {
          width: calc(340px + 12px);
          height: calc(50px + 12px);
        }

        @media (min-width: 480px) and (max-width: 759px) {
          width: calc(300px + 12px);
          height: calc(40px + 12px);
        }

        @media (max-width: 479px) {
          width: calc(280px + 12px);
          height: calc(35px + 12px);
        }
      }

      &:hover,
      &:focus {
        transform: translateY(-6px);
      }

      &:hover::before,
      &:focus::before {
        opacity: 1;
      }

      &::after {
        content: '';
        width: 30px;
        height: 30px;
        border-radius: 7px;
        border: 6px solid #14e956;
        position: absolute;
        z-index: -1;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        animation: ring 1.5s infinite;

        @media (min-width: 1025px) and (max-width: 1300px) {
          width: 26px;
          height: 26px;
        }

        @media (min-width: 760px) and (max-width: 1024px) {
          width: 24px;
          height: 24px;
        }

        @media (min-width: 480px) and (max-width: 759px) {
          width: 20px;
          height: 20px;
        }

        @media (max-width: 479px) {
          width: 19px;
          height: 19px;
        }
      }

      &:hover::after,
      &:focus::after {
        animation: none;
        display: none;
      }
    }
  }
}

.quote {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 10rem;
  margin-bottom: 10rem;
  position: relative;

  &.hidden {
    opacity: 0;
  }

  &.show {
    opacity: 1;

    .quote-body {
      animation: zoomIn 1.5s ease forwards;
    }
  }

  .quote-body {
    position: relative;
    width: 60%;
    height: 200px;
    background-color: transparent;
    /* background-image: url('./../../assets/images/quote.png'); */
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    grid-template-areas:
      'left . . .'
      '. quote quote .'
      '. quote quote .'
      '. . . right';
    justify-items: center;
    align-items: center;
    align-content: center;
    z-index: 10;
    border-radius: 15px;
    box-shadow: 0px 0px 1px 3px rgba(20, 233, 86, 0.98);
    -webkit-box-shadow: 0px 0px 1px 3px rgba(20, 233, 86, 0.98);
    -moz-box-shadow: 0px 0px 1px 3px rgba(20, 233, 86, 0.98);

    @media (min-width: 1025px) and (max-width: 1300px) {
      height: 190px;
      width: 65%;
    }

    @media (min-width: 760px) and (max-width: 1024px) {
      height: 180px;
      width: 70%;
    }

    @media (min-width: 480px) and (max-width: 759px) {
      height: 170px;
      width: 80%;
    }

    @media (max-width: 479px) {
      height: 160px;
      width: 87%;
    }

    .left {
      grid-area: left;
      width: 100px;
      height: 100px;
      margin-top: -2.4rem;
      z-index: 100;

      @media (min-width: 1025px) and (max-width: 1300px) {
        width: 90px;
        height: 90px;
        margin-top: -2.2rem;
      }

      @media (min-width: 760px) and (max-width: 1024px) {
        width: 80px;
        height: 80px;
        margin-top: -2rem;
      }

      @media (min-width: 480px) and (max-width: 759px) {
        width: 70px;
        height: 70px;
        margin-top: -1.8rem;
      }

      @media (max-width: 479px) {
        width: 60px;
        height: 60px;
        margin-top: -1.6rem;
      }
    }

    .quote-body-content {
      grid-area: quote;

      p {
        margin: 0;
        font-size: 2.5rem;
        font-weight: 900;
        text-transform: capitalize;
        opacity: 0.8;

        @media (min-width: 1025px) and (max-width: 1300px) {
          font-size: 2.3rem;
        }

        @media (min-width: 760px) and (max-width: 1024px) {
          font-size: 2rem;
        }

        @media (min-width: 480px) and (max-width: 759px) {
          font-size: 1.6rem;
        }

        @media (max-width: 479px) {
          font-size: 1.2rem;
        }

        span {
          font-size: 3rem;

          @media (min-width: 1025px) and (max-width: 1300px) {
            font-size: 2.6rem;
          }

          @media (min-width: 760px) and (max-width: 1024px) {
            font-size: 2.3rem;
          }

          @media (min-width: 480px) and (max-width: 759px) {
            font-size: 2rem;
          }

          @media (max-width: 479px) {
            font-size: 1.7rem;
          }
        }
      }
    }

    .right {
      grid-area: right;
      width: 100px;
      height: 100px;
      margin-bottom: -2.4rem;
      z-index: 100;

      @media (min-width: 1025px) and (max-width: 1300px) {
        width: 90px;
        height: 90px;
        margin-bottom: -2.2rem;
      }

      @media (min-width: 760px) and (max-width: 1024px) {
        width: 80px;
        height: 80px;
        margin-bottom: -2rem;
      }

      @media (min-width: 480px) and (max-width: 759px) {
        width: 70px;
        height: 70px;
        margin-bottom: -1.8rem;
      }

      @media (max-width: 479px) {
        width: 60px;
        height: 60px;
        margin-bottom: -1.6rem;
      }
    }
  }
}

.featured {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 5rem 0;
  margin-top: 3rem;

  &.hidden {
    opacity: 0;
  }

  &.show {
    opacity: 1;

    .featured-title {
      animation: flipInY 2s ease forwards;
      backface-visibility: visible !important;
    }

    .featured-box .article {
      animation: slideInRight 2s ease forwards;
    }
  }

  .featured-title {
    background: #12141d;
    border-radius: 12px;
    padding: 0.8rem 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 5rem;
    box-shadow: 0px 0px 4px 1px rgba(20, 233, 86, 1) inset;
    -webkit-box-shadow: 0px 0px 4px 1px rgba(20, 233, 86, 1) inset;
    -moz-box-shadow: 0px 0px 4px 1px rgba(20, 233, 86, 1) inset;
    transform: rotate(0.01turn);

    @media (min-width: 1025px) and (max-width: 1300px) {
      padding: 0.7rem 1.7rem;
    }

    @media (min-width: 760px) and (max-width: 1024px) {
      padding: 0.6rem 1.5rem;
    }

    @media (min-width: 480px) and (max-width: 759px) {
      padding: 0.5rem 1.3rem;
    }

    @media (max-width: 479px) {
      padding: 0.4rem 1.1rem;
    }

    h2 {
      text-transform: uppercase;
      font-family: 'Cyborg';
      font-weight: 900;
      color: #14e956;
      text-shadow: 3px 3px 0 #000, -1px -1px 0 #000,
        1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;

      @supports (-webkit-text-stroke: 0.1px #000) {
        -webkit-text-stroke: 0.1px #000;
        -webkit-text-fill-color: #14e956;
      }

      @media (min-width: 1025px) and (max-width: 1300px) {
        font-size: 2.5rem;
      }

      @media (min-width: 760px) and (max-width: 1024px) {
        font-size: 2.2rem;
      }

      @media (min-width: 480px) and (max-width: 759px) {
        font-size: 2rem;
      }

      @media (max-width: 479px) {
        font-size: 1.8rem;
      }
    }
  }

  .featured-box {
    display: flex;
    justify-content: center;
    align-items: center;

    .article {
      height: 35rem;
      width: 27rem;
      /* background-image: url('./../../assets//images//robo-mi.png'); */
      background-size: cover;
      background-position: right;
      border-radius: 15px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      @media (min-width: 480px) and (max-width: 759px) {
        height: 30rem;
        width: 22rem;
      }

      @media (max-width: 479px) {
        height: 27rem;
        width: 19rem;
      }

      .top {
        padding: 1rem;

        p {
          font-weight: 900;
          font-size: 1.2rem;
          background-color: rgba(0, 0, 0, 0.778);
          width: 60%;
          text-align: center;
          padding: 0.7rem 0;
          border-radius: 25px;

          @media (min-width: 480px) and (max-width: 759px) {
            font-size: 1rem;
            padding: 0.6rem 0;
          }

          @media (max-width: 479px) {
            font-size: 0.8rem;
            padding: 0.5rem 0;
          }

          span {
            color: #14e956;
            font-weight: 900;
          }
        }
      }

      .bottom {
        height: 40%;
        padding: 1rem;
        display: flex;
        flex-direction: column;
        justify-content: end;
        background: rgb(16, 233, 86);
        border-bottom-left-radius: 15px;
        border-bottom-right-radius: 15px;
        background: linear-gradient(0deg,
            rgba(16, 233, 86, 1) 0%,
            rgba(255, 255, 255, 0.0046612394957983305) 93%);

        h4 {
          font-weight: 700;

          @media (min-width: 480px) and (max-width: 759px) {
            font-size: 1.4rem;
          }

          @media (max-width: 479px) {
            font-size: 1.2rem;
          }
        }

        .info {
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding-right: 1rem;

          p {
            font-weight: 500;

            @media (min-width: 480px) and (max-width: 759px) {
              font-size: 1.1rem;
            }

            @media (max-width: 479px) {
              font-size: 1rem;
            }
          }

          a {
            animation: slide1 1s ease-in-out infinite;
          }
        }
      }
    }
  }
}

.slider {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: transparent;
  height: 80%;
  width: 100%;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.image {
  width: 700px;
  height: 398px;
  border-radius: 10px;

  @media (min-width: 1025px) and (max-width: 1300px) {
    width: 600px;
    height: 341px;
  }

  @media (min-width: 760px) and (max-width: 1024px) {
    width: 500px;
    height: 284px;
  }

  @media (min-width: 480px) and (max-width: 759px) {
    width: 300px;
    height: 170px;
  }

  @media (max-width: 479px) {
    width: 240px;
    height: 136px;
  }
}

.right-arrow {
  position: absolute;
  top: 50%;
  right: 32px;
  font-size: 3rem;
  color: #000;
  z-index: 10;
  cursor: pointer;
  user-select: none;
  transform: rotate(-0.1turn);
  opacity: 0.9;
  width: 90px;
  height: 88px;

  @media (min-width: 1025px) and (max-width: 1300px) {
    width: 80px;
    height: 78px;
    top: 42%;
    right: 62px;
  }

  @media (min-width: 760px) and (max-width: 1024px) {
    width: 70px;
    height: 68px;
    top: 42%;
    right: 62px;
  }

  @media (min-width: 480px) and (max-width: 759px) {
    width: 60px;
    height: 58px;
    top: 42%;
    right: 50px;
  }

  @media (max-width: 479px) {
    width: 45px;
    height: 40px;
    top: 42%;
    right: 9px;
  }

  &:hover,
  &:focus {
    transform: rotate(-0.1turn) scale(1.1);
  }
}

.left-arrow {
  position: absolute;
  top: 50%;
  left: 32px;
  font-size: 3rem;
  color: #000;
  z-index: 10;
  cursor: pointer;
  user-select: none;
  transform: rotate(0.6turn);
  opacity: 0.9;
  width: 90px;
  height: 88px;

  @media (min-width: 1025px) and (max-width: 1300px) {
    width: 80px;
    height: 78px;
    top: 42%;
    left: 62px;
  }

  @media (min-width: 760px) and (max-width: 1024px) {
    width: 70px;
    height: 68px;
    top: 42%;
    left: 62px;
  }

  @media (min-width: 480px) and (max-width: 759px) {
    width: 60px;
    height: 58px;
    top: 42%;
    left: 50px;
  }

  @media (max-width: 479px) {
    width: 45px;
    height: 40px;
    top: 42%;
    left: 9px;
  }

  &:hover,
  &:focus {
    transform: rotate(0.6turn) scale(1.1);
  }
}

.slide {
  position: absolute;
  top: 2%;
  right: 26%;
  opacity: 0;
  width: 700px;
  height: 70%;
  background: #fdfdfd;
  color: #1e2128;
  height: max-content;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  transition-duration: 1s ease;
  box-shadow: 0px 0px 18px 3px rgba(0, 0, 0, 0.6);
  -webkit-box-shadow: 0px 0px 18px 3px rgba(0, 0, 0, 0.6);
  -moz-box-shadow: 0px 0px 18px 3px rgba(0, 0, 0, 0.6);

  @media (min-width: 1025px) and (max-width: 1300px) {
    width: 600px;
    top: 5%;
    right: auto;
  }

  @media (min-width: 760px) and (max-width: 1024px) {
    width: 500px;
    top: 8%;
    right: auto;
  }

  @media (min-width: 480px) and (max-width: 759px) {
    width: 300px;
    top: 15%;
    right: auto;
  }

  @media (max-width: 479px) {
    width: 240px;
    top: 15%;
    right: auto;
  }
}

.slide.active {
  opacity: 1;
  transition-duration: 1s;
  transform: scale(1.08);
}

.detail {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-start;
  margin: 0;
  padding: 1rem;

  @media (min-width: 1025px) and (max-width: 1300px) {
    padding: 0.8rem;
  }

  @media (min-width: 760px) and (max-width: 1024px) {
    padding: 0.6rem;
  }

  @media (min-width: 480px) and (max-width: 759px) {
    padding: 0.5rem;
  }

  @media (max-width: 479px) {
    padding: 0.4rem;
  }

  h4 {
    margin: 0;
    font-family: 'Cyborg';
    font-size: 1.8rem;
    color: #14e956;
    text-shadow: 3px 3px 0 #1f2127, -1px -1px 0 #1f2127,
      1px -1px 0 #1f2127, -1px 1px 0 #1f2127,
      1px 1px 0 #1f2127;
    text-transform: uppercase;
    font-weight: 900;
    margin-bottom: 0.5rem;

    @media (min-width: 1025px) and (max-width: 1300px) {
      font-size: 1.5rem;
    }

    @media (min-width: 760px) and (max-width: 1024px) {
      font-size: 1.2rem;
    }

    @media (min-width: 480px) and (max-width: 759px) {
      font-size: 1.1rem;
    }

    @media (max-width: 479px) {
      font-size: 1rem;
    }
  }

  .desc {
    margin: 0;
    font-size: 18px;
    text-transform: capitalize;
    font-weight: 600;
    margin-bottom: 0.5rem;

    @media (min-width: 1025px) and (max-width: 1300px) {
      font-size: 16px;
    }

    @media (min-width: 760px) and (max-width: 1024px) {
      font-size: 14px;
    }

    @media (min-width: 480px) and (max-width: 759px) {
      font-size: 12px;
    }

    @media (max-width: 479px) {
      font-size: 11px;
    }
  }

  span {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 25%;

    @media (min-width: 760px) and (max-width: 1024px) {
      width: 33%;
    }

    @media (min-width: 480px) and (max-width: 759px) {
      width: 50%;
    }

    @media (max-width: 479px) {
      width: 70%;
    }

    .stack {
      margin: 0;
      background: #1e2128;
      color: #fffff4;
      padding: 0.3rem 0.6rem;
      text-transform: lowercase;
      font-weight: 700;
      border-radius: 25px;

      @media (min-width: 760px) and (max-width: 1024px) {
        font-size: 14px;
      }

      @media (min-width: 480px) and (max-width: 759px) {
        font-size: 13px;
      }

      @media (max-width: 479px) {
        font-size: 12px;
      }
    }

    a {
      margin: 0;
      border: 2px solid #1e2128;
      background: #fffff4;
      color: #1e2128;
      padding: 0.1rem 0.6rem;
      text-transform: lowercase;
      font-weight: 700;
      border-radius: 15px;

      @media (min-width: 760px) and (max-width: 1024px) {
        font-size: 14px;
      }

      @media (max-width: 479px) {
        font-size: 12px;
      }
    }
  }
}

@keyframes ring {
  0% {
    width: 30px;
    height: 30px;
    opacity: 1;
  }

  100% {
    width: 550px;
    height: 120px;
    opacity: 0;
  }
}

@media (min-width: 760px) and (max-width: 1024px) {
  @keyframes ring {
    0% {
      width: 28px;
      height: 28px;
      opacity: 1;
    }

    100% {
      width: 340px;
      height: 50px;
      opacity: 0;
    }
  }
}

@media (min-width: 480px) and (max-width: 759px) {
  @keyframes ring {
    0% {
      width: 24px;
      height: 24px;
      opacity: 1;
    }

    100% {
      width: 300px;
      height: 40px;
      opacity: 0;
    }
  }
}

@media (max-width: 479px) {
  @keyframes ring {
    0% {
      width: 22px;
      height: 22px;
      opacity: 1;
    }

    100% {
      width: 280px;
      height: 35px;
      opacity: 0;
    }
  }
}

@keyframes wobble {
  from {
    transform: translate3d(0, 0, 0) scale(1);
  }

  15% {
    transform: translate3d(-25%, 0, 0) rotate3d(0, 0, 1, -5deg) scale(1.1);
  }

  30% {
    transform: translate3d(20%, 0, 0) rotate3d(0, 0, 1, 3deg) scale(1.2);
  }

  45% {
    transform: translate3d(-15%, 0, 0) rotate3d(0, 0, 1, -3deg) scale(1.3);
  }

  60% {
    transform: translate3d(10%, 0, 0) rotate3d(0, 0, 1, 2deg) scale(1.2);
  }

  75% {
    transform: translate3d(-5%, 0, 0) rotate3d(0, 0, 1, -1deg) scale(1.1);
  }

  to {
    transform: translate3d(0, 0, 0) scale(1);
  }
}

@keyframes slide1 {

  0%,
  100% {
    transform: translate(0, 0);
  }

  50% {
    transform: translate(10px, 0);
  }
}

@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translate3d(-100%, 0, 0);
  }

  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translate3d(100%, 0, 0);
  }

  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

@keyframes flipInY {
  from {
    transform: perspective(400px) rotate3d(0, 1, 0, 90deg);
    animation-timing-function: ease-in;
    opacity: 0;
  }

  40% {
    transform: perspective(400px) rotate3d(0, 1, 0, -20deg);
    animation-timing-function: ease-in;
  }

  60% {
    transform: perspective(400px) rotate3d(0, 1, 0, 10deg);
    opacity: 1;
  }

  80% {
    transform: perspective(400px) rotate3d(0, 1, 0, -5deg);
  }

  to {
    transform: perspective(400px);
  }
}

@keyframes jackInTheBox {
  from {
    opacity: 0;
    transform: scale(0.1) rotate(30deg);
    transform-origin: center bottom;
  }

  50% {
    transform: rotate(-10deg);
  }

  70% {
    transform: rotate(3deg);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes rotateIn {
  from {
    transform: rotate3d(0, 0, 1, -200deg);
    opacity: 0;
  }

  to {
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}

@keyframes slideInUp {
  from {
    transform: translate3d(0, 100%, 0);
    visibility: visible;
  }

  to {
    transform: translate3d(0, 0, 0);
  }
}

@keyframes swing {
  20% {
    transform: rotate3d(0, 0, 1, 15deg);
  }

  40% {
    transform: rotate3d(0, 0, 1, -10deg);
  }

  60% {
    transform: rotate3d(0, 0, 1, 5deg);
  }

  80% {
    transform: rotate3d(0, 0, 1, -5deg);
  }

  to {
    transform: rotate3d(0, 0, 1, 0deg);
  }
}

@keyframes flash {

  from,
  50%,
  to {
    opacity: 1;
  }

  25%,
  75% {
    opacity: 0;
  }
}

@keyframes zoomIn {
  from {
    opacity: 0;
    transform: scale3d(0.3, 0.3, 0.3);
  }

  50% {
    opacity: 1;
  }
}

@keyframes slideInRight {
  from {
    transform: translate3d(100%, 0, 0);
    visibility: visible;
  }

  to {
    transform: translate3d(0, 0, 0);
  }
}

@keyframes typing {
  from {
    width: 0;
  }
}

@keyframes caret {
  50% {
    border-right-color: transparent;
  }
}

.content-tertiary {
  width: max-content;
  margin-bottom: 1.5rem;
}

.content-tertiary>p {
  background: #f7c292;
  border-radius: 12px;
  color: #ff7800 !important;
  font-size: 1.4rem;
  font-weight: 900;
  margin: 0;
  padding: 0.5rem 1rem;
  text-transform: capitalize;
  letter-spacing: 0.05em;


  @media (min-width: 1025px) and (max-width: 1300px) {
    font-size: 1.2rem;
  }

  @media (min-width: 760px) and (max-width: 1024px) {
    font-size: 1rem;
  }

  @media (min-width: 480px) and (max-width: 759px) {
    font-size: 0.85rem;
    padding: 0.27rem 0.7rem;
    display: flex;
    align-items: center;
  }

  @media (max-width: 479px) {
    font-size: 0.75rem;
    padding: 0.2rem 0.5rem;
    display: flex;
    align-items: center;
  }

  span {
    font-size: 1.7rem;
    animation: wobble 2.5s infinite;
    display: inline-block;

    @media (min-width: 1025px) and (max-width: 1300px) {
      font-size: 1.5rem;
    }

    @media (min-width: 760px) and (max-width: 1024px) {
      font-size: 1.2rem;
    }

    @media (min-width: 480px) and (max-width: 759px) {
      font-size: 1rem;
    }

    @media (max-width: 479px) {
      font-size: 0.9rem;
    }
  }
}